<template>
	<div class="tang_list">
		<div class="title">唐诗三百首</div>
		<mt-loadmore :top-method="loadTop" :bottom-method="loadBottom" :bottom-all-loaded="allLoaded" ref="loadmore">
			<div @click="goDetail(item)" class="listitem" v-for="(item,index) in tangshiList" :key="index">《{{item.name}}》---
				{{item.author}}</div>
		</mt-loadmore>
	</div>
</template>

<script>
	import { mapGetters } from 'vuex'
	import wxshare from '../utils/wxshare'
	export default {
		name: "tang_list",
		components: {
			
		},
		computed: {
			...mapGetters('getData', {
				tangshiList: 'tangshiList',
			}),
		},
		data() {
			return {
				allLoaded:false,
			};
		},
		created() {
			this.getData()
		},
		activated(){
			let self = this;
			wxshare({
				url:self.$route.fullPath,
				title:'一起背古诗',
				desc:'这些古诗你会背吗',
				imgUrl:'https://img.it610.com/image/info10/cea8417a946c47ccabbbecad8ec442cd.jpg',
			})
		},
		mounted() {},
		methods: {
			loadTop() {
				this.$store.dispatch('getData/getTangshiList', '').then(()=>{
					setTimeout(()=>{
						this.$refs.loadmore.onTopLoaded();
					},2000)
				})
			  
			},
			loadBottom() {
			  this.allLoaded = true;// 若数据已全部获取完毕
			  this.$refs.loadmore.onBottomLoaded();
			},
			getData() {
				this.$store.dispatch('getData/getTangshiList', '')
			},
			goDetail(item) {
				this.$router.push({
					path: "/tangshidetail",
					query: item,
				});
			},
		},
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
	.tang_list {
		text-align: center;
		font-size: 0.32rem;

		.title {
			font-size: 0.4rem;
			padding: 0.2rem;
		}

		.listitem {
			padding: 0.1rem;
		}
	}
</style>
